$midnight: #2c3e50;
$clouds: #ecf0f1;

/*-----------fonts---------*/
@font-face {
    font-family: 'AvenirNextCyr';
    src: url('../fonts/AvenirNextCyr-Regular.eot');
    src: local('Avenir Next Cyr Regular'), local('AvenirNextCyr-Regular'),
    url('../fonts/AvenirNextCyr-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/AvenirNextCyr-Regular.woff2') format('woff2'),
        url('../fonts/AvenirNextCyr-Regular.woff') format('woff'),
        url('../fonts/AvenirNextCyr-Regular.ttf') format('truetype');
    font-weight: normal;
}
@font-face {
    font-family: 'AvenirNextCyr';
    src: url('../fonts/AvenirNextCyr-Demi.eot');
    src: local('Avenir Next Cyr Demi'), local('AvenirNextCyr-Demi'),
    url('../fonts/AvenirNextCyr-Demi.eot?#iefix') format('embedded-opentype'),
    url('../fonts/AvenirNextCyr-Demi.woff2') format('woff2'),
    url('../fonts/AvenirNextCyr-Demi.woff') format('woff'),
    url('../fonts/AvenirNextCyr-Demi.ttf') format('truetype');
    font-weight: 500;
}
@font-face {
    font-family: 'AvenirNextCyr';
    src: url('../fonts/AvenirNextCyr-Bold.eot');
    src: local('Avenir Next Cyr Bold'), local('AvenirNextCyr-Bold'),
    url('../fonts/AvenirNextCyr-Bold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/AvenirNextCyr-Bold.woff2') format('woff2'),
    url('../fonts/AvenirNextCyr-Bold.woff') format('woff'),
    url('../fonts/AvenirNextCyr-Bold.ttf') format('truetype');
    font-weight: 700;
}
/*--------//fonts--------*/
* {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}
html {
  font-size:62.5%;
}
header, main, footer {
  width: 100%;
  height: auto;
}
.gridContainer {
  min-width: 320px;
  max-width: 1400px;
  margin: 0 auto;
  }
h1 {
  font:normal 3.8rem 'AvenirNextCyr';
  color: #F4A876;
  line-height: 150.5%;
}
h2 {
  font:normal 3.2rem 'AvenirNextCyr';
  color: #F4A876;
  line-height: 44px;
  text-align: center;
  letter-spacing: 0.05em;
}
h3 {
  font:normal 1.8rem 'AvenirNextCyr';
  line-height: 25px;
  color: #5C5C5C;
}
h4 {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #787878;
}
p {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 24px;
  text-align: center;
  color: #000000;
}
a {
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
}
button {
  cursor: pointer;
}
.wrapper img {
  width: 100%;
}
.colorOrange {
  color: #F4A876;
}
/*--------------------------- header ---------------------------*/
.flex .navMob {
  display: flex;
  justify-content: space-between;
}
.navMob .blockLogo {
  margin:20px 10px;
}
.navMob .blockBasket {
  margin:20px 10px;
}
nav a {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #FFFFFF;
}
.menu-phone .one {
  display: flex;
  height:70px;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
}
.menu-phone .navMenuUp {
  background: #ffffff;
}
.one .blockSearch {
  margin:15px 0;
  position:absolute;
  top:0;
  left:45px;
  z-index:1;
}
.one .blockMonLang {
  width:50%;
  margin:20px 0;
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  top:5px;
  right:0;
}
.navMenuUp a {
  font:normal 1.4rem 'AvenirNextCyr';
  line-height: 19px;
  color: #F4A876;
}
.callback .number {
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #5C5C5C;
}
.blockCallback .linkWhite {
  padding: 12px 13px;
  border: 1px solid #F4A876;
  text-align: center;
  font:normal 1.6rem 'AvenirNextCyr';
  line-height: 22px;
  color: #F4A876;
}
.linkWhite, .callback {
  margin: 15px;
}
.menu-phone .blockCallback {
  display:flex;
  background-color: #ffffff;
}
.blockMonLang form, .loginLink {
  font:normal 1.4rem 'AvenirNextCyr';
  line-height: 19px;
  color: #F4A876;
}
.blockMonLang .loginLink {
  padding: 0;
}
.blockSearch .formSearch {
  display:flex;
  padding: 5px;
  background: #ffffff;
}
.formSearch input[type="search"]:hover {
  width: 225px;
}
.blockSearch .formSearch:hover {
  border:.1rem solid #F4A876;
}
.formSearch input[type="search"] {
  width:0px;
  border-right:.2rem solid #E5E5E5;
  transition-property: width, border;
  transition-duration: 2s, 2s;
  transition-timing-function: ease, linear;
}
.formSearch input[type="submit"] {
  text-indent: -9999px;
  width: 35px;
  height:30px;
  background: url('../img/magnifying_glass 1.svg') right no-repeat;
  background-size: 70%;
}
/*--------------- screenSlider ----------------*/
.wrapper .screenSlider {
  position: relative;
  width:100vw;
  height:75vh;
  background:url('../img/image_slide.jpg') center no-repeat;
  background-size: cover;
}
.slider .blockText {
  padding:40px 10px;
  z-index: 1;
}
.blockText p {
  color:#FFFFFF;
  text-align: left;
}
.blockText .text1 {
  font:normal 3.6rem 'AvenirNextCyr';
  margin-bottom: 60px;
  position: relative;
}
.blockText .text1::after {
  position: absolute;
  content:'';
  width:135px;
  height:5px;
  z-index: 1;
  background-color:#F4A876;
  bottom:-30px;
  left:0;
}
.blockText .textBold {
  font:800 3.6rem 'AvenirNextCyr';
}
.blockText .text2 {
  margin-bottom: 30px;
}
.blockText .saleLink {
  padding:15px;
  font:normal 1.6rem 'AvenirNextCyr';
  text-align: center;
  color:#ffffff;
  background: #F4A876;
}
.screenSlider .slider {
  height:100%;
}
/*--------------- //screenSlider ----------------*/
/*--------------------------- foooter --------------------------*/
/*-------------------------- //foooter -------------------------*/
@media screen and (min-width:375px) {
  .callback .number {
    letter-spacing: 0.1em;
  }
  .blockCallback .linkWhite {
    letter-spacing: 0.21em;
  }
}
